





































































































/* 问卷标题 */
.fill .title{
	width: 90%;
	height: 3rem;
	line-height: 3rem;
	border: none;
	font-size: 1.2rem;
	color: #555;
	font-weight: bold;
	margin: 0 auto;
	text-align: center;
	margin-bottom: 1.5rem;
	cursor: default;
}
.fill .title:hover{
	background-color: #fef1e8;
}
/* 水平线 */
.fill hr{
	border: 1px solid #ccc;
	width: 93%;
	margin: 0 auto;
}
.fill ol li{
	display: -ms-flexbox;
	display: flex;
	-webkit-align-items: center;
}
.fill ol li span{
	margin-left: .5rem;
}



