
.date-wrap{
	display: inline-block;
}
.date-wrap input{
	width: 10rem;
	height: 2rem;
	border-radius: .2rem;
	padding-left: .5rem;
	border: 1px solid #888;
	cursor: pointer;
}
.date-wrap .date{
	position: absolute;
	z-index: 2;
	width: 15rem;
	font-size: 1rem;
	border: 1px solid #888;
	background-color: #fff;
	border-radius: .2;
	box-shadow: 0 .05em .25em rgba(0,0,0,.5);
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	margin-bottom: 5rem;
}
.date-wrap .year-month{
	position: relative;
	text-align: center;
	background-color: #ee7419;
	height: 2.6rem;
	line-height: 2.6rem;
	color: #fff;
}
.date-wrap .ymtext{
	cursor: default;
}
.date-wrap .switch{
	position: absolute;
	cursor: pointer;
	border: 7px solid transparent;
	top: .8rem;
}
.date-wrap .switch:hover{
	opacity: .8;
}
.date-wrap .pre-month{
	border-right-color: #fff;
	left: 1rem;
}
.date-wrap .next-month{
	border-left-color: #fff;
	right: 1rem;
}
.date-wrap .day ul{
	text-align: left;
	padding: .4rem;
}
.date-wrap .date ul li{
	width: 2rem;
	display: inline-block;
	list-style: none;
	border-radius: 5px;
	cursor: pointer;
	text-align: center;
	line-height: 2rem;
}
.date-wrap .date ul li:hover{
	background-color: #ee7419;
	border-radius: 5px;
	color: #fff;
}
.date-wrap .week{
	border-bottom: 1px solid #ccc;
}
.date-wrap .chosen{
	background-color: #ee7419;
	color: #fff;
}



