

































/* 问卷标题 */
.check .title{
	width: 90%;
	height: 3rem;
	line-height: 3rem;
	border: none;
	font-size: 1.2rem;
	color: #555;
	font-weight: bold;
	margin: 0 auto;
	text-align: center;
	margin-bottom: 1.5rem;
	cursor: default;
}
.check .title:hover{
	background-color: #fef1e8;
}
/* 水平线 */
.check hr{
	border: 1px solid #ccc;
	width: 93%;
	margin: 0 auto;
}



