




































































































/* 问题模块 */
.question-wrap{
	width: 90%;
	margin: 2rem auto;
	font-size: 1rem;
}
/* 单个问题 */
.question{
	padding: 1rem 1.5rem;
	text-align: left;
}
.question:hover{
	background-color: #fef1e8;
}
.question:hover input{
	background-color: #fef1e8;
}
.question:hover .operate{
	visibility: visible;
}
.question input{
	border: none;
	padding-left: .2rem;
}
.question input::-webkit-input-placeholder{
	color: black;
}
.question span{
	cursor: default;
}
.question li{
	list-style: none;
	line-height: 1.6rem;
}
.question li input{
	height: 1.6rem;
	font-size: 1rem;
}
.question .delOption{
	visibility: hidden;
	cursor: pointer;
}
.question .options{
	margin-left: 2rem;
}
.question .options:hover .delOption{
	visibility: visible;
}
.question .addOption{
	width: 50%;
	text-align: center;
	border: 1px dashed #888;
	cursor: pointer;
	margin-left: 2rem;
}
.question .operate{
	text-align: right;
	font-size: 1rem;
	visibility: hidden;
}
.question .operate span{
	cursor: pointer;
	margin-right: .5rem;
}
.question .radio{
	display: inline-block;
	width: .7rem;
	height: .7rem;
	border: 1px solid black;
	border-radius: 50%;
	box-shadow: 0 .05em .25em rgba(0,0,0,.5);
}
.question .checkbox{
	display: inline-block;
	width: .7rem;
	height: .7rem;
	border: 1px solid black;
	box-shadow: 0 .05em .25em rgba(0,0,0,.5);
}
.question .textarea{
	width: 90%;
	max-width: 90%;
	height: 5rem;
	margin-left: 2rem;
}
.question .required{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	margin-left: 2rem;
}



